import React from 'react';

export function SidePersInfo(props){
      const title = String(Object.keys(props)).split("_").join(" ");

      const handlePrintCv = function(){
            return window.print();
      }
	return (
      <>
            <h6 className="cv-h6 bg-info text-white p-2">{ title }</h6>
            <ul className="list-group">
            { props.personal_info.map( (item) => {
                  let combo = item.split("|");
            	return <li key={ item } className="list-group-item">
            	  <b className="cv-ul__b">{ combo[0] }</b>
                    { 
                      (combo[1].substr(0,4) === 'http') 
                        ? <a href={combo[1]} title={combo[1]} rel="noopener noreferrer" target="_blank">{combo[2]}</a>
                        : <p>{combo[1]}</p>
                     }
            	</li>
            })}
            <li className="list-group-item">
              <button className="btn btn-sm btn-success" onClick={ handlePrintCv }>Print CV-pdf</button>
            </li>
            </ul>
	</>
	)
}